* {
	box-sizing: border-box;
}
html {
	--bg: #fff;
	--fg: #25282A;
	--gray: #888;
	--royal: #0072CE;
	--blue: #6AD1E3;
	--primary: #D1CCBD;
	--secondary: var(--royal);
	--tertiary: #FF585D;
	background-color: var(--bg);
	color: var(--fg);
	font-family: -apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Helvetica,Arial,sans-serif;
	font-size: calc(.65em + 0.7vw);
	line-height: 1.3;
}
html ::selection {
	background: var(--secondary);
	color: var(--bg);
}
#page-wrapper {
	margin: auto;
	max-width: 50em;
}
fieldset {
	border: none;
	margin: 0;
	padding: 0;
}
.highlight {
	align-items: center;
	margin-bottom: 0.5em;
	width: 100%;
}
.highlight label {
	margin-right: 0.5em;
}
.highlight input {
	caret-color: var(--secondary);
}
label {
	color: var(--gray);
}
button, .highlight {
	border-radius: 5px;
	border-color: var(--primary);
	border: 0.1em solid var(--primary);
	padding: 0.5em 1em;
}
button:hover, button:focus, button:focus-within, .highlight:hover, .highlight:focus, .highlight:focus-within {
	border-color: var(--secondary);
}
input {
	border: none;
	flex: 1;
}
input:read-only {
	color: var(--primary);
}
input[type="number"] {
	text-align: center;
	border-bottom: 0.1em solid var(--primary);
	margin: 0 1em;
	width: 4em;
	padding-left: 0.5em;
}
input[type="number"]:hover, input[type="number"]:focus {
	border-color: var(--secondary);
}
button, input {
	background: transparent;
	color: inherit;
	cursor: pointer;
	font: inherit;
	outline: none;
}
button {
	position: relative;
	transition: top 50ms;
}
button:hover, button:focus {
	top: -1px;
}
button:active {
	background: var(--secondary);
	border-color: var(--secondary);
	top: 1px;
}
.border-white {
	border-top: 1px solid rgba(255, 255, 255, 0.2) !important;
}
